.bp--accordion {
    //transfer to a generalist style
    margin-right: 1rem;
    margin-left: 1rem;
    .card-header {
        a {
            color: $primary-color;
            text-decoration: none;
            i {
                color: $primary-color;
            }
        }
    }
    .card-block {
        padding: 0px;
        .list-group-item {
            &:first-child {
                border-top-right-radius: 0px;
                border-top-left-radius: 0px;
            }
            &.active {
                background-color: $primary-color;
                border-left: 1px solid rgba(0, 0, 0, .125);
                border-right: 1px solid rgba(0, 0, 0, .125);
            }
            .badge-default {
                background-color: $white-color;
                color: $primary-color;
            }
        }
    }
}