@import "typo-variables";
@font-face {
    font-family: 'bpone_bi';
    src : url('#{$icomoon-font-path}/bpone_bi.eot?a85ibl');
    src : url('#{$icomoon-font-path}/bpone_bi.eot?a85ibl#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/bpone_bi.ttf?a85ibl') format('truetype'),
    url('#{$icomoon-font-path}/bpone_bi.woff?a85ibl') format('woff'),
    url('#{$icomoon-font-path}/bpone_bi.svg?a85ibl#bpone_bi') format('svg');
    font-weight: normal;
    font-style: normal;
}

i {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'bpone_bi' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

